    left: -20%;
}

.container_login_box .col-md-7 {
    margin-top: 0;
}

.container_login_box h3 {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    margin-bottom: 30px;
}

.container_login_box h3 strong {
    font-size: 24px;
    font-weight: bold;
    color: #333;
}

.container_login_box .form-group label {
    display: block;
    font-size: 18px;
    font-weight: bold;
    margin-bottom: 10px;
    color: #333;
}

.container_login_box .form-control {
    height: 50px;
    font-size: 16px;
    color: #666;
    border: 1px solid #ccc !important;
    border-radius: 5px;
    padding: 10px;
    margin-bottom: 20px;
}

.container_login_box .form-control:focus {
    outline: none;
    box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25);
}

.container_login_box .btn {
    background-color: #007bff;
    color: #fff;
    border-radius: 5px;
    padding: 10px 20px;
    font-size: 18px;
    font-weight: bold;
    transition: background-color 0.3s ease;
    margin-top: 20px;
}

.container_login_box .btn:hover {
    background-color: #0062cc;
    color: #fff;
}

.container_login_box .forgot-pass {
    color: #007bff;
    text-decoration: none;
    font-weight: bold;
}

.container_login_box .forgot-pass:hover {
    color: #0056b3;
    text-decoration: none;
}

.msgNotification {
    color: rgb(255, 25, 0) !important;
    margin-bottom: 53px;
}
